import { Injectable, NgZone } from '@angular/core';
import { NotificationsHttpService } from '@backbase/data-ang/notifications';
import { asyncScheduler, BehaviorSubject, combineLatest, merge, Observable, of, ReplaySubject, Subject } from 'rxjs';
import { catchError, delay, filter, first, map, observeOn, pairwise, switchMap, tap } from 'rxjs/operators';
import { enterZone, leaveZone, shareReplayOnce } from '@backbase/notifications-common-ang';
import { WidgetPropertiesService } from './widget-properties.service';
import { HttpErrorResponse } from '@angular/common/http';

@Injectable()
export class NotificationsPollingService {
  private readonly forceUpdateCountersSubject = new Subject();

  private readonly requestsInterval$ = this.properties.unreadCountUpdateInterval$;

  private readonly repeat$ = new ReplaySubject(1);
  private readonly load$ = merge(
    of(''),
    this.forceUpdateCountersSubject,
    merge(this.forceUpdateCountersSubject, this.repeat$).pipe(
      switchMap(() => this.requestsInterval$),
      switchMap(interval => of('').pipe(delay(interval, leaveZone(this.ngZone, asyncScheduler)))),
      observeOn(enterZone(this.ngZone, asyncScheduler)),
    ),
  );

  private readonly updateCountersErrorSubject$$ = new BehaviorSubject<HttpErrorResponse | undefined>(undefined);

  /**
   * Stream with update counters request errors
   */
  readonly updateCountersError$ = this.updateCountersErrorSubject$$.asObservable();

  /**
   * Stream with unread notifications count
   */
  readonly unreadNotificationsCount$ = this.load$.pipe(
    switchMap(() => this.requestCount()),
    tap(() => this.repeat$.next()),
    filter(count => typeof count !== 'undefined'),
    shareReplayOnce(),
  );

  /**
   * Stream with flag of new available notifications
   */
  readonly newNotificationsAvailable$: Observable<boolean> = this.unreadNotificationsCount$.pipe(
    pairwise(),
    map(([prev, cur]) => {
      if (prev === null || prev === undefined) {
        return false;
      }
      if (cur === null || cur === undefined) {
        return false;
      }

      return cur - prev > 0;
    }),
    filter<boolean>(Boolean),
  );

  private requestCount() {
    const origins$ = this.properties.origins$;

    const severityLevels$ = this.properties.severityLevels$;

    return combineLatest([origins$, severityLevels$]).pipe(
      first(),
      switchMap(([origins, levels]) =>
        this.notificationHttpService.getUnreadCount({
          ...(origins.length && { origins }),
          ...(levels.length && { levels }),
        }),
      ),
      map(body => {
        this.updateCountersErrorSubject$$.next(undefined);

        return body?.unread;
      }),
      catchError((error: HttpErrorResponse) => {
        this.updateCountersErrorSubject$$.next(error);

        return of(undefined);
      }),
    );
  }

  constructor(
    private readonly notificationHttpService: NotificationsHttpService,
    private readonly properties: WidgetPropertiesService,
    private readonly ngZone: NgZone,
  ) {}

  /**
   * Method to trigger force update on unread notifications counter
   */
  forceUpdateCounters() {
    this.forceUpdateCountersSubject.next();
  }
}
