import { Injectable } from '@angular/core';

import {
  catchError,
  distinctUntilChanged,
  finalize,
  map,
  scan,
  share,
  switchMap,
  tap,
  withLatestFrom,
  startWith,
  filter,
} from 'rxjs/operators';
import { BehaviorSubject, combineLatest, Observable, of, Subject, zip } from 'rxjs';

import { NotificationsHttpService } from '@backbase/data-ang/notifications';
import { toISOExtendedFormat, NotificationItem } from '@backbase/notifications-common-ang';
import { WidgetPropertiesService } from './widget-properties.service';

interface RequestOptionsForNotificationsPage {
  from: number;
  createdOn?: string;
  append: boolean;
}

@Injectable()
export class NotificationsPaginationService {
  constructor(
    private readonly notificationHttpService: NotificationsHttpService,
    private readonly properties: WidgetPropertiesService,
  ) {}

  /**
   * Stream with page size
   */
  readonly sizeProperty$ = this.properties.pageSize$;

  private readonly pageSubject = new Subject<RequestOptionsForNotificationsPage>();
  private readonly page$: Observable<RequestOptionsForNotificationsPage> = this.pageSubject.asObservable();
  /**
   * Stream with current page number (work with loadPage method)
   */
  readonly pageFrom$ = this.page$.pipe(map(p => p.from));

  private readonly isLoadingSubject = new BehaviorSubject(false);

  /**
   * Stream with loading flag
   */
  readonly isLoading$ = this.isLoadingSubject.asObservable();

  // eslint-disable-next-line
  private readonly totalCountSubject = new Subject<number>();

  /**
   * Stream with total count of notifications
   */
  readonly totalCount$ = this.totalCountSubject.asObservable();

  private readonly origins$ = this.properties.origins$;
  private readonly severityLevels$ = this.properties.severityLevels$;

  private readonly notificationsRequest$ = combineLatest([
    this.page$,
    this.sizeProperty$,
    this.origins$,
    this.severityLevels$,
  ]).pipe(
    switchMap(([page, size, origins, levels]) => {
      this.isLoadingSubject.next(true);

      return this.notificationHttpService
        .getNotifications(
          {
            toDate:
              page.createdOn && new Date(toISOExtendedFormat(page.createdOn)).toISOString().replace(/\.\d+Z/, 'Z'),
            from: page.from,
            size,
            ...(origins.length && { origins }),
            ...(levels.length && { levels }),
          },
          'response',
        )
        .pipe(
          tap(res => {
            this.totalCountSubject.next(Number(res.headers.get('x-total-count') || '0'));
          }),
          map(({ body, headers }) => ({ data: body || [], headers, error: undefined })),
          catchError(error => of({ data: [] as NotificationItem[], headers: undefined, error })),
          finalize(() => {
            this.isLoadingSubject.next(false);
          }),
        );
    }),
    share(),
  );

  private readonly notificationsPage$ = this.notificationsRequest$.pipe(map(({ data }) => data));

  private readonly markAllNotificationsAsRead = new BehaviorSubject(false);

  /**
   * Stream of notifications
   */
  readonly notifications$: Observable<NotificationItem[]> = combineLatest([
    zip(this.page$, this.notificationsPage$).pipe(
      // reset accumulator for every first page in Append mode or for every page in regular mode
      scan(
        (
          allNotifications: NotificationItem[],
          [page, notifications]: [RequestOptionsForNotificationsPage, NotificationItem[]],
        ) => (!page.append || page.from === 0 ? [] : allNotifications).concat(notifications),
        [],
      ),
    ),
    this.markAllNotificationsAsRead.pipe(startWith(true), filter(Boolean)),
  ]).pipe(
    withLatestFrom(this.markAllNotificationsAsRead),
    // eslint-disable-next-line arrow-body-style
    map(([[notifications], allAsRead]) => {
      return allAsRead ? notifications.map(item => (!item.read && { ...item, read: true }) || item) : notifications;
    }),
  );

  /**
   * Stream of errors
   */
  readonly error$ = this.notificationsRequest$.pipe(
    map(({ error }) => error),
    distinctUntilChanged(),
  );

  private loadPageData(page: number, append = false, createdOn?: string) {
    this.pageSubject.next({ from: page, append, createdOn: createdOn });
  }

  /**
   * Method to load notifications of specified page
   *
   * @param page Page number
   */
  loadPage(page: number) {
    this.loadPageData(page);
  }

  /**
   * Method to load next page of notifications
   *
   * @param page Page number
   * @param createdOn Created on notification date
   */
  loadMore(page: number, createdOn?: string) {
    this.loadPageData(page, true, createdOn);
  }

  /**
   * Mark all notifications as read
   *
   * @param read Is read flag
   */
  markAllAsRead(read: boolean) {
    this.markAllNotificationsAsRead.next(read);
  }
}
