import { Injectable, Optional } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { first, map } from 'rxjs/operators';
import {
  getPropertyValue,
  modelItemToBoolean,
  secondsToMilliseconds,
  BaseWidgetPropertiesService,
  shareReplayOnce,
} from '@backbase/notifications-common-ang';
import { ItemModel } from '@backbase/foundation-ang/core';

export interface WidgetProperties {
  displayRelativeTime: boolean;
  allowedRoutes: string[];
  displaySettingsButton: boolean;
  notificationsMessageLineAmount: number;
  displayShowMoreButtonInBadge: boolean;
  pageSize: number;
  origins: string[];
  severityLevels: string[];
  unreadCountUpdateInterval: number;
}

export const DefaultWidgetProperties: WidgetProperties = {
  displayRelativeTime: false,
  allowedRoutes: [],
  displaySettingsButton: true,
  notificationsMessageLineAmount: 2,
  displayShowMoreButtonInBadge: false,
  pageSize: 5,
  origins: [],
  severityLevels: [],
  unreadCountUpdateInterval: 15,
};

@Injectable()
export class WidgetPropertiesService extends BaseWidgetPropertiesService {
  constructor(@Optional() protected readonly itemModel?: ItemModel) {
    super(itemModel);
  }

  private readonly displayRelativeTime$$ = new BehaviorSubject<boolean | undefined>(undefined);
  private readonly displaySettingsButton$$ = new BehaviorSubject<boolean | undefined>(undefined);
  private readonly notificationsMessageLineAmount$$ = new BehaviorSubject<number | undefined>(undefined);
  private readonly displayShowMoreButtonInBadge$$ = new BehaviorSubject<boolean | undefined>(undefined);
  private readonly pageSize$$ = new BehaviorSubject<number | undefined>(undefined);
  private readonly unreadCountUpdateInterval$$ = new BehaviorSubject<number | undefined>(undefined);

  readonly displayRelativeTime$ = getPropertyValue(this.displayRelativeTime$$, this.displayRelativeTime);
  readonly displaySettingsButton$ = getPropertyValue(this.displaySettingsButton$$, this.displaySettingsButton);
  readonly notificationsMessageLineAmount$ = getPropertyValue(
    this.notificationsMessageLineAmount$$,
    this.notificationsMessageLineAmount,
  );
  readonly displayShowMoreButtonInBadge$ = getPropertyValue(
    this.displayShowMoreButtonInBadge$$,
    this.displayShowMoreButtonInBadge,
  );
  readonly pageSize$ = getPropertyValue(this.pageSize$$, this.pageSize);
  readonly unreadCountUpdateInterval$ = getPropertyValue(
    this.unreadCountUpdateInterval$$,
    this.unreadCountUpdateInterval,
  );

  private get displayRelativeTime(): Observable<boolean> {
    if (!this.itemModel) {
      return this.defaultModelObservable(DefaultWidgetProperties.displayRelativeTime);
    }

    return this.itemModel
      .property('displayRelativeTime', DefaultWidgetProperties.displayRelativeTime)
      .pipe(map(modelItemToBoolean), first());
  }

  private get displaySettingsButton(): Observable<boolean> {
    if (!this.itemModel) {
      return this.defaultModelObservable(DefaultWidgetProperties.displaySettingsButton);
    }

    return this.itemModel
      .property('displaySettingsButton', DefaultWidgetProperties.displaySettingsButton)
      .pipe(map(modelItemToBoolean), first());
  }

  private get notificationsMessageLineAmount(): Observable<number> {
    if (!this.itemModel) {
      return this.defaultModelObservable(DefaultWidgetProperties.notificationsMessageLineAmount);
    }

    return this.itemModel
      .property('notificationsMessageLineAmount', DefaultWidgetProperties.notificationsMessageLineAmount)
      .pipe(map(Number), first());
  }

  private get displayShowMoreButtonInBadge(): Observable<boolean> {
    if (!this.itemModel) {
      return this.defaultModelObservable(DefaultWidgetProperties.displayShowMoreButtonInBadge);
    }

    return this.itemModel
      .property('displayShowMoreButtonInBadge', DefaultWidgetProperties.displayShowMoreButtonInBadge)
      .pipe(map(modelItemToBoolean), first());
  }

  private get pageSize(): Observable<number> {
    if (!this.itemModel) {
      return this.defaultModelObservable(DefaultWidgetProperties.pageSize);
    }

    return this.itemModel.property('pageSize', DefaultWidgetProperties.pageSize).pipe(map(Number), shareReplayOnce());
  }

  private get unreadCountUpdateInterval(): Observable<number> {
    if (!this.itemModel) {
      return this.defaultModelObservable(DefaultWidgetProperties.unreadCountUpdateInterval * 1000);
    }

    return this.itemModel.property('unreadCountUpdateInterval', DefaultWidgetProperties.unreadCountUpdateInterval).pipe(
      map(Number),
      map(n => n * 1000),
      shareReplayOnce(),
    );
  }

  setDisplayRelativeTime(value: string | boolean): void {
    this.displayRelativeTime$$.next(modelItemToBoolean(value));
  }

  setDisplaySettingsButton(value: string | boolean): void {
    this.displaySettingsButton$$.next(modelItemToBoolean(value));
  }

  setNotificationsMessageLineAmount(value: string | number): void {
    this.notificationsMessageLineAmount$$.next(Number(value));
  }

  setDisplayShowMoreButtonInBadge(value: string | boolean): void {
    this.displayShowMoreButtonInBadge$$.next(modelItemToBoolean(value));
  }

  setPageSize(value: string | number): void {
    this.pageSize$$.next(Number(value));
  }

  setUnreadCountUpdateInterval(value: string | number): void {
    const updateInterval = Number(value);
    if (!isNaN(updateInterval) && updateInterval > 0) {
      this.unreadCountUpdateInterval$$.next(secondsToMilliseconds(updateInterval));
    }
  }
}
