import {
  Component,
  Directive,
  ElementRef,
  EventEmitter,
  Inject,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { HttpErrorResponse } from '@angular/common/http';
import { asyncScheduler, BehaviorSubject, combineLatest, Observable, of, Subject } from 'rxjs';
import {
  filter,
  first,
  map,
  mapTo,
  observeOn,
  pairwise,
  skip,
  skipUntil,
  startWith,
  switchMap,
  take,
  takeUntil,
  tap,
} from 'rxjs/operators';
import { NotificationItem, NotificationStreamItem, shareReplayOnce } from '@backbase/notifications-common-ang';
import { NotificationService } from '@backbase/ui-ang/notification';
import { Pubsub, PUBSUB } from '@backbase/foundation-ang/web-sdk';

import { NotificationsPaginationService } from '../../services/notifications-pagination.service';
import { NotificationsPollingService } from '../../services/notifications-polling.service';
import { NotificationsDataService } from '../../services/notifications-data.service';
import { NotificationsRouterService } from '../../services/notifications-router.service';
import { WidgetPropertiesService } from '../../services/widget-properties.service';

export const NOTIFICATIONS_BADGE_RELOAD_ON_NAVIGATE_EVENT = 'bb.event.notifications-badge.reload-on-navigate';

@Component({
  selector: 'bb-notifications-list',
  templateUrl: './notifications-list.component.html',
})
export class NotificationsListComponent implements OnInit, OnDestroy {
  /**
   * Navigation event.
   */
  @Output() navigation = new EventEmitter<void>();

  /**
   * Preselected notification
   */
  @Input() set preselectedNotification(notification: NotificationItem | undefined) {
    this.selectedNotification = notification;
  }

  /**
   * Format to display date in past
   */
  @Input() dateFormat: string | undefined;

  private readonly componentDestroyed$ = new Subject<void>();

  /**
   * Stream of routes available for navigation.
   */
  readonly allowedRoutes$: Observable<string[]> = this.properties.allowedRoutes$;

  /**
   * Stream of a flag that defines if we should display settings button.
   */
  readonly displaySettingsButton$: Observable<boolean> = this.properties.displaySettingsButton$;

  constructor(
    private readonly routerService: NotificationsRouterService,
    private readonly notificationsPaginationService: NotificationsPaginationService,
    private readonly notificationsPollingService: NotificationsPollingService,
    private readonly notificationService: NotificationsDataService,
    private readonly showNotificationService: NotificationService,
    private readonly element: ElementRef,
    private readonly properties: WidgetPropertiesService,
    @Inject(PUBSUB) private readonly pubSub: Pubsub,
  ) {}

  /**
   * Stream with notifications items.
   */
  readonly notifications$: Observable<NotificationStreamItem[]> =
    this.notificationsPaginationService.notifications$.pipe(takeUntil(this.componentDestroyed$), shareReplayOnce());

  /**
   * Stream with error while loading of notifications.
   */
  readonly error$ = this.notificationsPaginationService.error$;

  /**
   * Stream with error while loading of notifications.
   */
  readonly updateCountersError$ = this.notificationsPollingService.updateCountersError$;

  /**
   * Mark as read request error stream which shows if request has failed.
   */
  readonly markAllAsReadError$ = this.notificationService.markAllNotificationAsReadError;

  /**
   * Stream with flag if notifications is loading now or no.
   */
  readonly isLoading$ = this.notificationsPaginationService.isLoading$;

  private readonly firstNewNotificationId$ = this.notifications$.pipe(
    pairwise(),
    map(([prevNotifications, newNotifications]) => {
      let firstNewNotification = newNotifications[prevNotifications.length];

      // reset notification to the first page scenario
      if (prevNotifications.length >= newNotifications.length) {
        firstNewNotification = newNotifications[0];
      }

      return firstNewNotification && firstNewNotification.id;
    }),
  );

  private readonly notificationWasReadSubject = new Subject<boolean>();
  private readonly notificationWasRead$ = this.notificationWasReadSubject.asObservable();

  private readonly newNotificationsAvailableSubject = new BehaviorSubject<boolean>(false);
  private readonly updateCounters$ = this.newNotificationsAvailableSubject.pipe(
    startWith(false),
    filter(i => !i),
    mapTo(true),
    shareReplayOnce(),
  );

  /**
   * Stream with total count of notifications.
   */
  readonly totalCount$ = this.updateCounters$.pipe(switchMap(() => this.notificationsPaginationService.totalCount$));

  /**
   * Stream with notifications about new notification available
   */
  readonly newNotificationsAvailable$ = this.newNotificationsAvailableSubject.asObservable();

  /**
   * Stream with page size.
   */
  readonly pageSize$ = this.notificationsPaginationService.sizeProperty$;

  /**
   * Stream with unread notifications count
   */
  readonly unreadCount$ = this.updateCounters$.pipe(
    switchMap(() =>
      this.notificationsPollingService.unreadNotificationsCount$.pipe(
        switchMap(counter =>
          this.notificationWasRead$.pipe(
            startWith(false),
            map(read => {
              if (!read || !counter) {
                return counter;
              }

              return --counter;
            }),
          ),
        ),
      ),
    ),
  );

  private lastSelectedNotificationItemButton: HTMLElement | undefined;

  /**
   * Steam with the starting page number.
   */
  readonly pageFrom$ = this.notificationsPaginationService.pageFrom$;

  /**
   * Notification item that is shown in notification details now.
   */
  selectedNotification: NotificationItem | undefined;

  ngOnInit() {
    this.firstNewNotificationId$
      .pipe(
        filter(Boolean),
        map(id => `.bb-nt-${id}`),
        observeOn(asyncScheduler),
        map(elemClass => this.element.nativeElement.querySelector(elemClass) as HTMLElement),
        filter<HTMLElement>(Boolean),
        takeUntil(this.componentDestroyed$),
      )
      .subscribe((elemScrollTo: HTMLElement) => {
        (elemScrollTo.firstChild as HTMLElement)?.focus();
        elemScrollTo.scrollIntoView({ behavior: 'smooth' });
      });

    this.notificationsPollingService.newNotificationsAvailable$
      .pipe(takeUntil(this.componentDestroyed$))
      .subscribe(value => {
        this.newNotificationsAvailableSubject.next(value);
      });

    this.onLoadNewNotifications();
  }

  ngOnDestroy() {
    this.componentDestroyed$.next();
    this.componentDestroyed$.complete();
  }

  /**
   * Method to mark all notifications as read.
   */
  onReadAllNotifications() {
    this.notificationService.markAllNotificationAsRead().subscribe(() => {
      this.notificationsPaginationService.markAllAsRead(true);
    });
  }

  /**
   * Method to load new notifications by reloading of the first page.
   */
  onLoadNewNotifications() {
    this.notificationsPollingService.unreadNotificationsCount$
      .pipe(
        skip(1), // skip stored value to wait for unread notifications count load occurred
        first(),
      )
      .subscribe(() => {
        this.newNotificationsAvailableSubject.next(false);
        this.notificationsPaginationService.markAllAsRead(false);
        this.notificationsPaginationService.loadPage(0);
      });
    this.notificationsPollingService.forceUpdateCounters();
  }

  /**
   * Method to load next notifications page.
   */
  onLoadMore(totalCount: number, $event: { from: number }) {
    const asyncNotifications$ = this.notifications$.pipe(
      skipUntil(of(undefined).pipe(observeOn(asyncScheduler))), // wait for new asynchronously loaded notifications
    );

    const lastNotification$ = this.notifications$.pipe(
      first(),
      map(notifications => notifications[notifications.length - 1]),
      map(({ id }) => `.bb-nt-${id} [data-role="notifications-list-item"]`),
      observeOn(asyncScheduler),
      map(selector => this.element.nativeElement.querySelector(selector)),
    );

    this.notifications$.pipe(first()).subscribe(data => {
      const createdOn = data[0].createdOn;
      this.notificationsPaginationService.loadMore($event.from, createdOn);
    });

    combineLatest([asyncNotifications$, of(totalCount)])
      .pipe(
        take(1),
        filter(([notifications, total]) => notifications.length >= total),
        switchMap(() => lastNotification$),
      )
      .subscribe(lastNotification => lastNotification.focus());
  }

  /**
   * Method to open notification settings.
   */
  onNotificationsSettingsOpen() {
    this.routerService.emitSettingsOpenedNavigation();
    this.navigation.emit();
  }

  /**
   * Method to show notification details view or to navigate to the notification routing URL if routing is enabled.
   */
  selectNotification(clickEvent: Event, notification: NotificationItem, openDetailsView?: boolean) {
    clickEvent.preventDefault();
    this.allowedRoutes$.subscribe(routes => {
      const route = notification.routing && notification.routing['where-to'];

      if (!openDetailsView && notification.routing && routes.some(allowedRoute => route === allowedRoute)) {
        this.pubSub.publish(NOTIFICATIONS_BADGE_RELOAD_ON_NAVIGATE_EVENT, undefined);
        this.routerService.emitNotificationRouting(notification.routing);
        this.navigation.emit();
      } else {
        this.selectedNotification = notification;
        this.saveRefToNotification(clickEvent);
      }
    });

    if (!notification.read) {
      this.notificationService.markNotificationAsRead(notification.id).subscribe(() => {
        notification.read = true;
        this.notificationWasReadSubject.next(true);
      });
    }
  }

  /**
   * Method to delete notification.
   */
  deleteNotification() {
    if (this.selectedNotification) {
      this.notificationService
        .deleteNotification(this.selectedNotification.id)
        .pipe(
          take(1),
          tap(() => this.notificationsPaginationService.loadPage(0)),
        )
        .subscribe(
          () => {
            this.selectedNotification = undefined;
          },
          error => {
            this.showNotificationService.showNotification({
              header: this.getErrorMessage(error),
              modifier: 'error',
              message: '',
            });
          },
        );
    }
  }

  /**
   * Method to unselect notification and return back focus position.
   */
  unselectNotification() {
    this.selectedNotification = undefined;
    this.restoreFocusOnNotification();
  }

  /**
   * Method to differentiate notifications by IDs.
   *
   * @param _index List item index
   * @param notification Notification list item
   * @returns Notification id
   */
  trackById(_index: number, notification: NotificationItem): string {
    return notification.id;
  }

  private saveRefToNotification(notificationClickEvent: any) {
    this.lastSelectedNotificationItemButton = notificationClickEvent.target.closest(
      '[data-role="notifications-list-item"]',
    );
  }

  private restoreFocusOnNotification() {
    setTimeout(() => {
      if (!this.lastSelectedNotificationItemButton) {
        return;
      }

      this.lastSelectedNotificationItemButton.focus();
    }, 0);
  }

  private getErrorMessage(error: HttpErrorResponse | Error | string): string {
    return typeof error !== 'string'
      ? (error as HttpErrorResponse)?.error?.message || error.message || 'Unknown Error'
      : error;
  }
}

@Directive({
  selector: '[bbNotificationsListCustomizable]',
})
export class NotificationsListCustomizableDirective extends BbTemplate<
  NotificationsListComponent,
  {
    notifications: NotificationItem[];
    error: any;
    updateCountersError: HttpErrorResponse | undefined;
    isLoading: boolean;
    totalCount: number;
    pageSize: number;
    newNotificationsAvailable: boolean;
    unreadCount: number;
    markAllAsReadError: boolean;
  }
> {}
